import { useState, useEffect } from "react";

const scrollTopOrBottom = () =>  window.scrollY === 0 || window.innerHeight + window.scrollY === document.body.offsetHeight;

export default (cb, threshold = 0, throttleTime = 300) => {
  const [position, setPosition] = useState(0);
  let throttleTimeout = null;

  const scrollTimeoutCallback = () => {
    let direction = null;
    if (!scrollTopOrBottom()) {
      // null if top/bottom
      if (position > window.pageYOffset + threshold) direction = "up";
      else if (position < window.pageYOffset - threshold) direction = "down";
    }
    if (direction) {
      //not null
      setPosition(window.pageYOffset); // save position
      cb(direction); // run callback
    }
    throttleTimeout = null;
  };
  const handleScroll = () => {
    if (throttleTimeout === null) {
      throttleTimeout = setTimeout(scrollTimeoutCallback, throttleTime); //error
    }
  };
  useEffect(() => {
    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll); //cleanup
  });
};
