import React, {useEffect} from "react";
import { createPortal } from "react-dom";
import "./modal.css";

// We get hold of the div with the id modal in index.html
const modalRoot = document.getElementById("modal");

function Modal(props) {
    const element = document.createElement("div");
    element.className = "modal-background";
  // We append the created div to the div#modal
    useEffect(() => {
        modalRoot.appendChild(element);
        return () => {
            modalRoot.removeChild(element);
        };
    }, [])
  /*
   * We remove the created div when this Modal Component is unmounted
   * Used to clean up the memory to avoid memory leak
   */

    return createPortal(
      props.children,
      element
    );
}
export default Modal;
