import React from "react";
import "./StartupProjects.css";

export default function StartupProject(props) {

  return (
    <div className="main" id="projects">
      <div>
        <h1 className="skills-heading">Projects</h1>
        <p className="subTitle project-subtitle">
          SOME STARTUPS AND COMPANIES THAT I HELP TO CREATE THEIR TECH.
        </p>
        <div className="startup-projects-main">
          <a
            href="#projects/modal"
            className="startup-project-text"
          >
            <div className="liveu-div" onClick={() => props.projectClicked(0)}>
              <img
                alt="LiveU"
                src={require("../../assests/images/LiveU_Trans(On_White)_300x300.png")}
              ></img>
            </div>
            <div className="lbd-div" onClick={() => props.projectClicked(1)}>
              <img
                alt="LBD"
                src={require("../../assests/images/lbd_logo_light.png")}
              ></img>
            </div>
            <div className="attollo-div" onClick={() => props.projectClicked(2)}>
              <img
                alt="attollo"
                src={require("../../assests/images/attollo-logo.png")}
              ></img>
            </div>
          </a>
        </div>
      </div>
    </div>
  );
}
