import React from "react";
import useScroll from "../../assests/hooks/useScroll";
import "./Header.css";


  
  function handleScroll(direction) {
    const navbar = document.querySelector(".header")
    if (direction !== "down") {
      navbar.classList.remove("scrolling-down");
    } else {
      navbar.classList.add("scrolling-down");
    }
  }
  
  
  
  function Header() {
    useScroll(handleScroll, 110);
  return (
      <header className="header">
        <a href="#logo" id="logo" className="logo">
          <span className="grey-color"> &lt;</span>
          <span className="logo-name">Nehemia lilintal</span>
          <span className="grey-color">/&gt;</span>
        </a>
        <input className="menu-btn" type="checkbox" id="menu-btn" />
        <label className="menu-icon" htmlFor="menu-btn">
          <span className="navicon"></span>
        </label>
        <ul className="menu">
          <li>
            <a href="#skills">Skills</a>
          </li>
          <li>
            <a href="#projects">Projects</a>
          </li>
          <li>
            <a href="#contact">Contact Me</a>
          </li>
        </ul>
      </header>
  );
}
export default Header;
