import React, { useState, useRef } from "react";
import Header from "../components/header/Header";
import Greeting from "./greeting/Greeting";
import Skills from "./skills/Skills";
import StartupProject from "./StartupProjects/StartupProject";
import ExtendedProjects from "./StartupProjects/extendedProjects";
import Footer from "./contact/Contact";
import Modal from "../components/modal/modal";

export default function Main () {
  const [modalOpen, setModalOpen] = useState(false);
  const [modalIndex, setModalIndex] = useState(0);
  const Y = useRef(0);
  function handleModalOpen( open ){ // prevent background scrolling
    const body = document.body;
    if(open){
        Y.current = window.scrollY;
        body.style.position = "fixed"; // fix for moblie devices
    } else {
      body.style.overflow = "";
      body.style.position = "";
      window.scrollTo(0, Y.current); // return to previous position
    }
    setModalOpen(open);
  };
  window.onhashchange = e => {
    console.log("hash change", e)
    if (modalOpen && ~e.oldURL.indexOf("/modal")) {
      // we back to home from
      handleModalOpen(false);
    }
  }
  function projectClicked(index) {
    handleModalOpen(true);
    setModalIndex(index);
  };
  return (
    <main>
      <Header />
      <div className="code-back">
        <Greeting />
        <Skills />
        <StartupProject
          projectClicked={projectClicked}
        />
      </div>
      <Footer />
      {modalOpen && (
        <Modal>
          <div className="my-modal" onDoubleClick={() => handleModalOpen(false)}>
            <ExtendedProjects
              modalIndex={modalIndex}
              setModalIndex={setModalIndex}
              handleClick={() => handleModalOpen(false)}
            />
          </div>
        </Modal>
      )}
    </main>
  );
}
