import React, { useState,useReducer } from "react";
import "./StartupProjects.css";
import styled from "styled-components";
import SwipeableViews from "react-swipeable-views";
import { autoPlay, bindKeyboard } from "react-swipeable-views-utils";
import Emoji from "../../components/emoji/emoji";
import LiveU from "./extendedProjects/liveU"
const AdvancedSwipeableViews = bindKeyboard(autoPlay(SwipeableViews));
/* culculate index based on params
  @param current <Number> current index
  @param dir <String> direction ("prev / next")
  @param length <Number> max index to wrap
  @return <Number> culculated index
*/
function navigateHelper(current, length){
  if (current < 0) {
    return length - 1;
  } 
  if(current > length -1) {
    return 0;
  }
  return current;
}

function navigateReducer(state, action) {
  switch (action.type) {
    case "autoplay":
      return { ...state, autoplay: !state.autoplay };
    case "changeIndex":
      return { ...state, index: action.payload };
    default:
      throw new Error();
  }
}

export default function ExtendedProject(props) {
  const initState = { index: props.modalIndex, autoplay: true };
  const [state, dispatch] = useReducer(navigateReducer, initState);
  const itemsLength = 3;
 function navigateClick(e) {
   e.stopPropagation();
   const i =
     e.currentTarget.name === "prev" ? state.index - 1 : state.index + 1;
   dispatch({
     type: "changeIndex",
     payload: navigateHelper(i, itemsLength)
   });
 }
  function openProjectInNewWindow(url) {
    var win = window.open(url, "_blank");
    win.focus();
  }

  return (
    <div className="main" id="projects">
      <div>
        <div className="startup-projects-main">
          <NavButton
            className="player-buttons right"
            title={`close modal`}
            name="close"
            onClick={props.handleClick}
          >
            <Emoji emoji={"❌"} />
          </NavButton>
          <div
            className="player-buttons left"
            onDoubleClick={e => e.stopPropagation()}
            title={`${state.autoplay ? "pause" : "continue"} autoplay`}
          >
            <NavButton name="prev" onClick={navigateClick}>
              <Emoji emoji={"⏪"} />
            </NavButton>
            <NavButton onClick={() => dispatch({ type: "autoplay" })}>
              <Emoji emoji={state.autoplay ? "⏸️" : "▶️"} />
            </NavButton>
            <NavButton name="next" onClick={navigateClick}>
              <Emoji emoji={"⏩"} />
            </NavButton>
          </div>
          <div className="startup-project-text">
            <AdvancedSwipeableViews
              containerStyle={{ maxWidth: "80vw" }}
              onChangeIndex={i => dispatch({ type: "changeIndex", payload: i })}
              index={state.index}
              interval={5000}
              autoplay={state.autoplay}
            >
              <LiveU openProjectInNewWindow={openProjectInNewWindow} />
              <div>
                <div
                  className="lbd-div"
                  onClick={() => openProjectInNewWindow("https://lbd.co.il/")}
                >
                  <img
                    alt="LBD"
                    src={require("../../assests/images/lbd_logo_light.png")}
                  ></img>
                </div>
                <p>
                  <b>this section under construction</b>
                  <br />
                  <Emoji emoji="🚧🚧🚧🚧🚧🚧" />
                </p>
              </div>
              <div>
                <div
                  className="attollo-div"
                  onClick={() =>
                    openProjectInNewWindow("http://attollo.io/index.html")
                  }
                >
                  <img
                    alt="attollo"
                    src={require("../../assests/images/attollo-logo.png")}
                  ></img>
                </div>
                <p>
                  <b>this section under construction</b>
                  <br />
                  <Emoji emoji="🚧🚧🚧🚧🚧🚧" />
                </p>
              </div>
            </AdvancedSwipeableViews>
          </div>
        </div>
      </div>
    </div>
  );
}

const NavButton = styled.button`
  z-index: 2;
  padding: 8px 1px;
  border-radius: 20px;
  cursor: pointer;
  background-color: unset;
  border:unset

  &:hover {
    box-shadow: 0 3px 3px 0px rgba(0, 0, 0, 0.4);
  }
  &:active {
    margin-top: 1px;
  }
`;