import React from "react";


export default function LiveU({openProjectInNewWindow}) {
    return (
      <div
        className="liveu-div"
        >
        <img
        onClick={() => openProjectInNewWindow("https://www.liveu.tv/")}
          alt="LiveU"
          src={require("../../../assests/images/LiveU_Trans(On_White)_300x300.png")}
        ></img>
        <p className="subTitle">
          LiveU is company that providing live video streaming for TV, mobile,
          online and social media.
          <br />I worked 2 years as front-end engineer to build and maintain GUI
          of several embedded devices (LU200, LU300, LU500, LU600).
          <br />
          <b>Technologies: AngularJS, python.
          <br /> 
          Workflow: gerrit, agile.</b>
        </p>
        <span className="img-gallery">
          <img
            alt="LiveU"
            src={require("../../../assests/images/lu600-databridge-gui.jpg")}
          ></img>
          <img
            alt="LiveU"
            src={require("../../../assests/images/LU600-Interface.jpg")}
          ></img>
          <img
            alt="LiveU"
            src={require("../../../assests/images/lu300-gui.jpg")}
          ></img>
        </span>
      </div>
    );
}